/**
 * @summary Decodifica uma string em Base64 para uma string de texto UTF-8.
 *
 * @description
 * Esta fun√ß√£o fornece uma maneira robusta e compat√≠vel com m√∫ltiplos ambientes (Node.js e navegadores)
 * para decodificar uma string Base64. Ela detecta automaticamente o ambiente de execu√ß√£o e utiliza
 * as APIs nativas mais apropriadas para garantir a m√°xima performance e corretude.
 *
 * A implementa√ß√£o lida corretamente com caracteres multi-byte (UTF-8), como acentos e emojis,
 * que s√£o frequentemente corrompidos pela fun√ß√£o `atob()` nativa do navegador.
 *
 * @param {string} [text=""] - A string em formato Base64 a ser decodificada.
 *
 * @returns {string} A string decodificada em UTF-8. Retorna uma string vazia se a entrada
 * for inv√°lida, vazia ou se ocorrer um erro durante a decodifica√ß√£o.
 *
 * @example
 * // Decodificando texto ASCII simples
 * const hello = base64From('SGVsbG8gV29ybGQh');
 * console.log(hello); // "Hello World!"
 *
 * // Decodificando texto com caracteres UTF-8 (acentos e s√≠mbolos)
 * const complex = base64From('U3VjZXNzbyEg4pyT');
 * console.log(complex); // "Sucesso! ‚úì"
 */
function base64From(text = "") {
  // Valida a entrada para garantir que √© uma string n√£o vazia.
  if (typeof text !== "string" || text.length === 0) {
    return "";
  }

  try {
    // A maneira padr√£o de verificar se o c√≥digo est√° rodando fora de um navegador (ex: Node.js).
    if (typeof window === "undefined") {
      // **Ambiente Node.js:**
      // Utiliza a classe `Buffer`, que √© altamente otimizada e a forma can√¥nica
      // de lidar com dados bin√°rios e encodings no Node.js.
      return Buffer.from(text, "base64").toString("utf-8");
    }
    // **Ambiente do Navegador:**
    // A fun√ß√£o `atob()` sozinha n√£o lida bem com caracteres UTF-8.
    // A abordagem moderna abaixo garante a decodifica√ß√£o correta.
    const binaryString = window.atob(text);

    // Converte a string bin√°ria decodificada em um array de bytes (Uint8Array).
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; i++) {
      bytes[i] = binaryString.charCodeAt(i);
    }

    // A API `TextDecoder` interpreta corretamente o array de bytes como uma string UTF-8.
    return new window.TextDecoder().decode(bytes);
  } catch (error) {
    // Captura exce√ß√µes que podem ocorrer se a string `text` n√£o for um Base64 v√°lido.
    // Retorna uma string vazia para um comportamento consistente e previs√≠vel.
    // console.error("Falha ao decodificar Base64:", error); // Descomente para depura√ß√£o
    return "";
  }
}

// ------------------------------------------------------------------------------------------------

module.exports = base64From;
