  // O uso da flag /g garante que todas as ocorr√™ncias sejam substitu√≠das.
  const base64 = urlSafeBase64.replace(/-/g, "+").replace(/_/g, "/");

  // 2. Calcula e adiciona o preenchimento ('=') de forma eficiente.
