/**
 * @summary Compara dois ArrayBuffers para verificar se cont√™m os mesmos bytes.
 *
 * @description
 * Realiza uma compara√ß√£o bin√°ria eficiente de dois ArrayBuffers. A fun√ß√£o √© otimizada
 * para diferentes ambientes: no Node.js, utiliza o m√©todo nativo e r√°pido `Buffer.equals()`,
 * enquanto no navegador, emprega uma t√©cnica de compara√ß√£o por blocos para acelerar o processo.
 *
 * @param {ArrayBuffer} buffer1 - O primeiro ArrayBuffer para a compara√ß√£o.
 * @param {ArrayBuffer} buffer2 - O segundo ArrayBuffer para a compara√ß√£o.
 *
 * @returns {boolean} Retorna `true` se os buffers forem id√™nticos, caso contr√°rio, `false`.
 *
 * @example
 * const buf1 = new Uint8Array([1, 2, 3, 4, 5]).buffer;
 * const buf2 = new Uint8Array([1, 2, 3, 4, 5]).buffer;
 * const buf3 = new Uint8Array([1, 2, 3, 4, 9]).buffer;
 *
 * console.log(bufferCompare(buf1, buf2)); // true
 * console.log(bufferCompare(buf1, buf3)); // false
 */
function bufferCompare(buffer1, buffer2) {
  // 1. Valida√ß√£o de tipo: garante que ambos os argumentos s√£o inst√¢ncias de ArrayBuffer.
  if (!(buffer1 instanceof ArrayBuffer) || !(buffer2 instanceof ArrayBuffer)) {
    return false;
  }

  // 2. Verifica√ß√£o de tamanho: a forma mais r√°pida de identificar buffers diferentes.
  // Se os tamanhos n√£o batem, √© imposs√≠vel que sejam iguais.
  if (buffer1.byteLength !== buffer2.byteLength) {
    return false;
  }

  // **Otimiza√ß√£o para ambiente Node.js:**
  if (typeof window === 'undefined') {
    // Converte os ArrayBuffers para Buffers do Node.js e usa o m√©todo `equals`,
    // que √© uma implementa√ß√£o nativa e extremamente perform√°tica.
    const buf1 = Buffer.from(buffer1);
    const buf2 = Buffer.from(buffer2);
    return buf1.equals(buf2);
  }

  // **Otimiza√ß√£o para ambiente do Navegador:**
  // Em vez de comparar byte a byte, compara em blocos maiores (4 bytes ou 32 bits por vez).
  const view1_32 = new Uint32Array(buffer1);
  const view2_32 = new Uint32Array(buffer2);

  // Compara a maior parte do buffer em blocos de 32 bits.
  for (let i = 0; i < view1_32.length; i++) {
    if (view1_32[i] !== view2_32[i]) {
      return false;
    }
  }

  // Calcula onde a compara√ß√£o de 32 bits parou para checar os bytes restantes.
  const remainingOffset = view1_32.length * 4;

  // Compara os bytes restantes (caso o tamanho do buffer n√£o seja m√∫ltiplo de 4).
  const view1_8 = new Uint8Array(buffer1);
  const view2_8 = new Uint8Array(buffer2);
  for (let i = remainingOffset; i < view1_8.length; i++) {
    if (view1_8[i] !== view2_8[i]) {
      return false;
    }
  }

  // Se todas as compara√ß√µes passaram, os buffers s√£o id√™nticos.
  return true;
}

