const { encrypt } = require("../crypto");
const base64To = require("../utils/base64To");

// ------------------------------------------------------------------------------------------------

/**
 * @summary Encripta uma mensagem em peda√ßos (chunks) usando RSA-OAEP.
 *
 * @description
 * Esta fun√ß√£o ass√≠ncrona primeiro converte a mensagem para uma string Base64 para garantir
 * a consist√™ncia dos dados. Em seguida, divide essa string em peda√ßos e encripta cada
 * um em paralelo para m√°xima performance. O resultado √© um array de strings, onde
 * cada uma representa um peda√ßo encriptado.
 *
 * @param {CryptoKey} publicKey - A chave p√∫blica RSA (formato `CryptoKey`) a ser usada.
 * @param {string} message - A mensagem de texto a ser encriptada.
 * @param {object} [props={}] - Propriedades adicionais para a encripta√ß√£o.
 * @param {number} [props.chunkSize=190] - O tamanho de cada peda√ßo da string Base64.
 *
 * @returns {Promise<string[]>} Uma Promise que resolve para um array de peda√ßos encriptados.
 */
async function messageEncryptToChunks(publicKey, message, props = {}) {
  // 1. Converte a mensagem para Base64 PRIMEIRO.
  // Esta etapa √© crucial para a consist√™ncia do sistema e o tratamento de caracteres especiais.
  const message64 = base64To(message);

  const chunkSize = props.chunkSize || 190;
  const chunks = [];

  // 2. Divide a string Base64 em peda√ßos (chunks).
  for (let i = 0; i < message64.length; i += chunkSize) {
    chunks.push(message64.substring(i, i + chunkSize));
  }

  // 3. Cria um array de Promises para encriptar cada peda√ßo da string Base64.
  const encryptionPromises = chunks.map(chunk => 
    encrypt(publicKey, chunk, props)
  );

  // 4. Executa todas as encripta√ß√µes em paralelo para m√°xima performance.
  return Promise.all(encryptionPromises);
}

// ------------------------------------------------------------------------------------------------

module.exports = messageEncryptToChunks;